@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}

















































































































































































.calendar-date-range-table {
  &__inner {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: $cellHeight;

    &--desable {
      background-color: $colorBase300;
      color: $colorBase600;
      cursor: text;
      pointer-events: none;
    }

    &:before {
      position: absolute;
      top: 50%;
      z-index: 0;
      margin-top: -12px;
      height: 24px;
      background-color: $colorBlue600;
      content: "";
    }

    &--range {
      &:before {
        left: 0;
        width: 100%;
      }
    }

    &--range-start {
      &:before {
        right: 0;
        width: 50%;
      }
    }

    &--range-end {
      &:before {
        left: 0;
        width: 50%;
      }
    }
  }

  &__date {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 22px;
    height: 22px;
    border: 2px solid $colorClear;
    border-radius: 50%;
    transition: background-color 0.3s ease;

    &:hover {
      &:not(.calendar-date-range-table__date--selected) {
        background-color: $colorBlue750;
        cursor: pointer;
      }
    }

    &--today {
      border-color: $colorBlue800;
      color: $colorBlue900;
    }

    &--selected {
      background-color: $colorBlue900;
      color: $colorWhite;
      cursor: pointer;
    }

    &--another-month {
      color: $colorBase600;
    }
  }
}
