@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}



































































































.calendar-date-table {
  &__inner {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: $cellHeight;

    &:not(.calendar-date-table__inner--desable):hover {
      color: $colorBlue800;
      cursor: pointer;
    }

    &--desable {
      background-color: $colorBase300;
      color: $colorBase600;
      cursor: text;
      pointer-events: none;
    }
  }

  &__date {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    border: 2px solid $colorClear;
    border-radius: 50%;

    &--today {
      border-color: $colorBlue800;
      color: $colorBlue900;
    }

    &--selected {
      background-color: $colorBlue800;
      color: $colorWhite;
    }

    &--another-month {
      color: $colorBase600;
    }
  }
}
