@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}







































.align-change {
  margin-top: -5px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;

  &__label {
    font-weight: bold;
  }

  &__select {
    margin-left: 20px;
    display: flex;
    li {
      padding: 8px 5px;
      background-color: #fff;
      border: 1px solid #ccc;
      border-radius: 5px;
      width: 85px;
      text-align: center;
      cursor: pointer;

      &:hover {
        background-color: #f2f5fc;
      }

      &.current {
        background-color: #f2f5fc;
        border-color: #3468eb;
        cursor: text;
        pointer-events: none;
      }

      &:not(:first-child) {
        margin-left: 10px;
      }
    }
  }
}
