@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}










































.input {
  padding: 0 8px;
  width: 100%;
  height: $formPartsHeight;
  border: 1px solid $colorBase600;
  border-radius: $sizeRadius;
  background-color: $colorWhite;
  box-shadow: 0 0 3px rgba($colorBlue900, 0);
  color: $colorText;
  font-size: $basicFontSize;
  line-height: $formPartsHeight;
  transition: box-shadow 0.3s ease, border-color 0.3s ease;

  &::placeholder {
    color: $colorBase700;
  }

  &:disabled {
    border-color: $colorBase500;
    background-color: $colorBase300;
    color: $colorBase600;
  }

  &--focus {
    border-color: $colorBlue800;
    box-shadow: 0 0 3px rgba($colorBlue900, 0.7);
  }
}
