@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}




































































































































































































































































































































































































.date-picker-range-change-input {
  position: relative;

  &__container {
    display: flex;
    align-items: center;
  }

  &__field {
    position: relative;
    flex: 1;
  }

  &__input--hidden {
    opacity: 0;
  }

  &__date {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 5px;
    background-color: $colorBlue600;

    &::before,
    &::after {
      position: absolute;
      top: 50%;
      left: 10px;
      transform: translateY(-50%);
    }

    &::before {
      content: attr(data-before-value);
    }

    &::after {
      content: attr(data-after-value);
    }

    &--start-switch {
      &::after {
        opacity: 0;
      }
    }

    &--end-switch {
      &::after {
        opacity: 0;
      }
    }
  }

  &__hyphen {
    padding: 0 10px;
  }

  &__delete-button {
    position: relative;
    margin-left: 8px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: $colorBase500;
    cursor: pointer;

    &:hover {
      background-color: $colorBase600;
    }

    &::before,
    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -4px;
      margin-left: -1px;
      width: 1px;
      height: 8px;
      background-color: $colorWhite;
      content: "";
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }

  &__popup {
    position: absolute;
    top: 40px;
    left: 50%;
    min-width: 700px;
    width: 100%;
    background-color: $colorWhite;
    box-shadow: 0 1px 4px rgba(#000, 0.1);
    transform: translateX(-50%);
  }

  &__header {
    padding: 10px;
    border-bottom: 1px solid $colorBase400;
  }

  &__body {
    padding: 10px;
  }
}

.datePickerPopup-enter-active,
.datePickerPopup-leave-active {
  margin-top: 0;
  opacity: 1;
  transition: opacity 0.3s ease, margin-top 0.3s ease;
}
.datePickerPopup-enter,
.datePickerPopup-leave-to {
  margin-top: -40px;
  opacity: 0;
}

.fade-enter-active {
  transition: all 0.3s ease;
}
.fade-leave-active {
  transition: all 0.3s ease;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

@keyframes start-anime {
  0% {
    opacity: 1;
    transform: translate(0, -50%);
  }
  50% {
    opacity: 0;
    transform: translate(50%, -50%);
  }
  100% {
    opacity: 0;
    transform: translate(50%, -50%);
  }
}

@keyframes end-anime {
  0% {
    opacity: 1;
    transform: translate(0, -50%);
  }
  50% {
    opacity: 0;
    transform: translate(-50%, -50%);
  }
  100% {
    opacity: 0;
    transform: translate(-50%, -50%);
  }
}
