@charset "utf-8";

@import "../../../assets/scss/_webfont";
@import "../../../assets/scss/_element-ui";
@import "../../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}
























































































































































































































































.date-range-confirm-cansel-input-picker {
  position: relative;

  &__header {
    display: flex;
    padding: 10px;
    border-bottom: 1px solid $colorBase400;
  }

  &__body {
    padding: 5px 10px 10px;
  }

  &__footer {
    display: flex;
    justify-content: flex-end;
    padding: 10px;
    border-top: 1px solid $colorBase500;
  }

  &__popup {
    position: absolute;
    top: 40px;
    width: 750px;
    background-color: $colorWhite;
    box-shadow: 2px 2px 5px $colorShadow100;

    &--left {
      left: 0;
    }

    &--center {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__direct {
    padding: 5px;
    min-width: 85px;
    border: 1px solid $colorBase400;
    border-radius: 5px;
    background-color: $colorWhite;
    text-align: center;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: $colorBlue600;
    }

    &:not(:first-child) {
      margin-left: 5px;
    }

    &--current {
      background-color: $colorBlue600;
      font-weight: bold;
      cursor: text;
    }
  }

  &__button {
    margin-left: 10px;
    padding: 10px 10px 8px;
    min-width: 85px;
    border: 1px solid;
    border-radius: 5px;
    background-color: $colorWhite;
    text-align: center;
    font-size: 12px;
    line-height: 1;
    cursor: pointer;
    transition: border-color 0.3s ease, background-color 0.3s ease;

    &--cancel {
      border-color: $colorBase500;
      color: $colorBase800;

      &:hover {
        background-color: $colorBlue600;
      }
    }

    &--apply {
      border-color: $colorBlue900;
      background-color: $colorBlue900;
      color: $colorWhite;

      &:hover {
        border-color: $colorBlue800;
        background-color: $colorBlue800;
      }
    }
  }
}

.datePickerPopup-enter-active,
.datePickerPopup-leave-active {
  margin-top: 0;
  opacity: 1;
  transition: opacity 0.3s ease, margin-top 0.3s ease;
}
.datePickerPopup-enter,
.datePickerPopup-leave-to {
  margin-top: -40px;
  opacity: 0;
}
