@charset "utf-8";

@import "../../../assets/scss/_webfont";
@import "../../../assets/scss/_element-ui";
@import "../../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}


















































































.date-range-picker-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1px;
  border: 1px solid $colorBase600;
  border-radius: 5px;

  &--focus {
    border-color: $colorWhite;
    box-shadow: 0 0 2px 2px $colorBlue900;
    transition: box-shadow 0.3s ease;
  }

  &--disabled {
    border-color: $colorBase500;
    background-color: $colorBase300;
  }

  &__input {
    flex: 1;
    height: $formPartsHeight;
    border: 2px solid rgba(0, 0, 0, 0);
    border-radius: 5px;
    text-align: center;
    font-size: $basicFontSize;
    line-height: $formPartsHeight;
    &:disabled {
      background-color: $colorBase300;
      color: $colorBase600;
    }

    &--focus {
      background-color: $colorBlue700;
    }
  }

  &__hyphen {
    padding: 0 10px;
    width: 30px;
    text-align: center;
    line-height: 1;
  }
}
