@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}









































































































.date-range-picker-popup {
  position: relative;
  background-color: $colorWhite;

  &__previous-button-container {
    position: absolute;
    top: 5px;
    left: 0;
    display: flex;
  }

  &__next-button-container {
    position: absolute;
    top: 5px;
    right: 0;
    display: flex;
  }

  &__button {
    position: relative;
    width: 12px;
    height: 15px;
    background-color: $colorWhite;
    line-height: 1;
    cursor: pointer;
    transition: border-color 0.3s ease, background-color 0.3s ease;

    &::before {
      position: absolute;
      top: 7px;
      width: 7px;
      height: 7px;
      border-top: 1px solid $colorBase700;
      content: "";
      transition: border-color 0.3s ease;
    }

    &:hover {
      &::before {
        border-color: $colorBase500;
      }
    }
  }

  &__previous {
    &::before {
      left: 8px;
      border-left: 1px solid $colorBase700;
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }

  &__next {
    &::before {
      right: 8px;
      border-right: 1px solid $colorBase700;
      transform: translate(50%, -50%) rotate(45deg);
    }
  }

  &__skip-button {
    position: relative;
    width: 12px;
    height: 15px;
    background-color: $colorWhite;
    line-height: 1;
    cursor: pointer;
    transition: border-color 0.3s ease, background-color 0.3s ease;

    &::before,
    &::after {
      position: absolute;
      top: 7px;
      width: 7px;
      height: 7px;
      border-top: 1px solid $colorBase700;
      content: "";
      transition: border-color 0.3s ease;
    }

    &:hover {
      &::before,
      &::after {
        border-color: $colorBase500;
      }
    }
  }

  &__one-year-ago {
    margin-right: 8px;

    &::before,
    &::after {
      border-left: 1px solid $colorBase700;
      transform: translate(-50%, -50%) rotate(-45deg);
    }

    &::before {
      left: 7px;
    }
    &::after {
      left: 10px;
    }
  }

  &__one-year-later {
    margin-left: 8px;

    &::before,
    &::after {
      border-right: 1px solid $colorBase700;
      transform: translate(50%, -50%) rotate(45deg);
    }

    &::before {
      right: 7px;
    }
    &::after {
      right: 10px;
    }
  }

  &__body {
    display: flex;
    align-items: center;
  }

  &__calendar {
    &:not(:first-child) {
      margin-left: 20px;
    }
  }
}
