@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}























































































































.date-picker-input {
  position: relative;

  &__field {
    padding: 0 10px;
    width: 100%;
    height: $formPartsHeight;
    border: 1px solid $colorBase600;
    border-radius: $sizeRadius;
    box-shadow: 0 0 3px rgba($colorBlue900, 0);
    font-size: $basicFontSize;
    line-height: $formPartsHeight;
    transition: box-shadow 0.3s ease, border-color 0.3s ease;
    &::placeholder {
      color: $colorBase700;
    }

    &:disabled {
      border-color: $colorBase500;
      background-color: $colorBase300;
      color: $colorBase600;
    }

    &--focus {
      border-color: $colorBlue800;
      box-shadow: 0 0 3px rgba($colorBlue900, 0.7);
    }
  }
  &__popup {
    position: absolute;
    top: 40px;
    left: 50%;
    padding: 10px;
    min-width: 700px;
    width: 100%;
    box-shadow: 0 1px 4px rgba(#000, 0.1);
    transform: translateX(-50%);
  }
}

.datePickerPopup-enter-active,
.datePickerPopup-leave-active {
  margin-top: 0;
  opacity: 1;
  transition: opacity 0.3s ease, margin-top 0.3s ease;
}

.datePickerPopup-enter,
.datePickerPopup-leave-to {
  margin-top: -40px;
  opacity: 0;
}
