@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}




































































































































































































































































































































.date-picker-range-input {
  position: relative;

  &__container {
    display: flex;
    align-items: center;
  }

  &__input {
    flex: 1;
  }

  &__hyphen {
    padding: 0 8px;
  }

  &__delete-button {
    position: relative;
    margin-left: 8px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: $colorBase500;
    cursor: pointer;

    &:hover {
      background-color: $colorBase600;
    }

    &::before,
    &::after {
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -4px;
      margin-left: -1px;
      width: 1px;
      height: 8px;
      background-color: $colorWhite;
      content: "";
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }

  &__popup {
    position: absolute;
    top: 40px;
    left: 50%;
    min-width: 700px;
    width: 100%;
    background-color: $colorWhite;
    box-shadow: 0 1px 4px rgba(#000, 0.1);
    transform: translateX(-50%);
  }

  &__header {
    padding: 10px;
    border-bottom: 1px solid $colorBase400;
  }

  &__body {
    padding: 10px;
  }
}

.datePickerPopup-enter-active,
.datePickerPopup-leave-active {
  margin-top: 0;
  opacity: 1;
  transition: opacity 0.3s ease, margin-top 0.3s ease;
}
.datePickerPopup-enter,
.datePickerPopup-leave-to {
  margin-top: -40px;
  opacity: 0;
}
